import Vue from 'vue';
import VueRouter from 'vue-router';
import Files from '../views/Files.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'Files',
    component: Files
  },
  {
    path: '/screenshots',
    name: 'Screenshot',
    component: () => import('../views/Screenshot.vue')
  },
  {
    path: '/htmltotext',
    name: 'HTMLtoTXT',
    component: () => import('../views/HTMLtoTXT.vue')
  },
  {
    path: '/plist',
    name: 'Plist',
    component: () => import('../views/Plist.vue')
  },
   {
   path: '/archive',
    name: 'Archive',
    component: () => import('../views/Archive.vue')
  },
   {
   path: '/helpers',
    name: 'Helpers',
		component: () => import('../views/Helpers.vue'),
		children: [
			{
				path: `image_sizes`,
				component: () => import('../views/ImageSizes.vue'),
			}
		]
  },
  {
    path: '/settings',
    name: 'Settings',
    component: () => import('../views/Settings.vue')
  }
];

const router = new VueRouter({
  routes
});

export default router;
