<template>
  <div class="header">
    <q-header elevated reveal>
      <div class="grid">
        <q-toolbar>
          <q-tabs shrink>
            <q-route-tab
              v-for="(route, index) of routes"
              :key="index"
              :label="route.label"
              :to="route.to"
              :exact="route.exact"
            />
          </q-tabs>
          <q-space />
          <span class="folder-name">{{ folderName }}</span>
        </q-toolbar>
      </div>
    </q-header>
  </div>
</template>

 <script>
import { mapState } from 'vuex';

export default {
  name: 'Header',
  data: function () {
    return {
      routes: [
        {
          to: '/',
          label: 'Файлы',
          exact: true,
        },
        {
          to: '/screenshots',
          label: 'Скриншоты',
          exact: true,
        },
        {
          to: '/htmltotext',
          label: 'HTML→TXT',
          exact: true,
        },
        {
          to: '/plist',
          label: 'Plist',
          exact: true,
        },
        {
          to: '/archive',
          label: 'Check / Архив',
          exact: true,
        },
        {
          to: '/helpers/image_sizes',
          label: 'Helpers',
          exact: true,
        },
        {
          to: '/settings',
          label: 'Настройки',
          exact: true,
        },
      ],
    };
  },
  computed: {
    ...mapState({
      folderName: (state) => state.folderName,
    }),
  },
  watch: {},
  methods: {},
  mounted() {},
  created() {},
};
</script>

 <style  lang="scss" scoped>
.header {
  padding-bottom: 2rem;
}
.folder-name{
	padding-left: 1rem;
}
</style>