<template>
  <div class="files">
    <q-card
      @click.native="setFolderName(folder)"
      v-for="(folder, i) of arrFolderName"
      :key="i"
      class="item folder-card"
    >
      <q-card-section>{{ folder }}</q-card-section>
    </q-card>
  </div>
</template>

 <script>
import { mapState } from 'vuex';

export default {
  name: 'Files',
  data: function () {
    return {
      arrFolderName: [],
    };
  },
  computed: {
    ...mapState({
      baseURL: (state) => state.baseURL,
    }),
    folderName: {
      get() {
        return this.$store.state.folderName;
      },
      set(value) {
        this.$store.commit('setFolderName', value);
      },
    },
    redirectAfterFiles: {
      get() {
        return this.$store.state.redirectAfterFiles;
      },
      set(value) {
        this.$store.commit('setRedirectAfterFiles', value);
      },
    },
  },
  watch: {},
  methods: {
    async getFolders() {
      let response, result;
      try {
        response = await fetch(this.baseURL);
        result = await response.json();
      } catch (error) {
        console.error('getFolders', error);
      }
      this.arrFolderName = result;
    },
    setFolderName(name) {
      this.folderName = name;
      this.$router.push(this.redirectAfterFiles);
    },
  },
  mounted() {},
  created() {
    this.getFolders();
  },
};
</script>

 <style lang="scss" scoped>
.folder-card {
  cursor: pointer;
  margin-bottom: 1rem;
}
</style>