/* eslint-disable */
const merge = require('deepmerge');

export default {
  state: {
    options: {
      files: {},
      screenshot: {},
      HTMLtoTXT: {},
      plist: {},
      checkArchive: {
        autoCheck: false
      },
      helpers: {
        isRatio: null
      }
    }
  },
  mutations: {
    setGlobalOptions(state) {
      let serialOptions = JSON.stringify(state.options);
      localStorage.setItem('builderGlobalOptions', serialOptions);
    },
    getGlobalOptions(state) {
      let optionsFromStorage = JSON.parse(
        localStorage.getItem('builderGlobalOptions')
      );
      if (!optionsFromStorage) {
        return;
      }

      state.options = merge(state.options, optionsFromStorage);
    }
  },
  actions: {},
  getters: {}
};
